export default {
  data() {
    return {
      // 密码输入的内容
      nicknameVal: "",
      // 控制按钮的颜色
      isActive: false,
      // 控制清楚小图标的显示与隐藏
      isIcon: false
    };
  },
  methods: {
    // 当密码框有输入内容的时候
    inputVal() {
      this.isActive = true;
      this.isIcon = true;
    },
    // 当密码框失去焦点的时候
    inputBlur() {
      if (this.nicknameVal !== "") {
        return (this.isActive = true);
      }
      return (this.isActive = false), (this.isIcon = false);
    },
    // 点击小图标清楚 input 里面的内容
    clearVal() {
      this.nicknameVal = "";
      this.isIcon = false;
    }
  }
};
